import {Greeting, GreetRequest} from "./greet/greet_pb";


import {GreetServiceClient} from "./greet/greet_grpc_web_pb";

const client = new GreetServiceClient('https://greeter-web.asdf.ooo');
const request = new GreetRequest();
const greeting = new Greeting();
greeting.setFirstName('Raphael');
greeting.setLastName('Bicker')
request.setGreeting(greeting);

client.greet(request, {}, (err, response) => {
    console.log('result', response.getResult());
    console.log('error', err);
});

console.log('hello asdf');